/* Import Google Fonts */
/* If you chose Option B above, this line is already included */
/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto:wght@400;500&display=swap'); */

/* Global Styles */
body {
    font-family: 'Roboto', sans-serif; /* Set default font to Roboto */
    margin: 0;
    padding: 0;
    background-color: #3D5A80; /* Ensure body background matches container */
}

/* Container Setup */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #3D5A80; /* Deep blue background */
    padding-top: 30px;
    padding-bottom: 60px;
    min-height: 100vh;
    position: relative;
    overflow: auto;
}

/* Logo and Header */
.logoContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 30%;
    padding: 0 20px;
}

.logo {
    width: 60%;
    height: 30%;
    margin-bottom: 10px;
}

.mainHeading {
    font-family: 'Montserrat', sans-serif; /* Use Montserrat for main headings */
    font-size: 28px;
    font-weight: 700; /* Bold weight */
    color: #FFEEBC; /* Light cream color for better contrast */
    margin: 20px 0;
}

/* Back Button */
.customBackButton {
    display: flex;
    align-items: center;
    background-color: #EE6C4D; /* Matching the main heading color */
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px;
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

.customBackButton:hover {
    background-color: #EE6C4DCC; /* Semi-transparent on hover */
}

.customBackButtonText {
    font-size: 18px;
    margin-left: 10px;
    color: #FFF;
    font-weight: 500; /* Medium weight */
}

/* Content Area */
.content {
    flex: 1;
    width: 100%;
    padding: 20px;
    overflow-y: auto;
    box-sizing: border-box;
}

/* Student List */
.studentList {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    padding: 0 20px;
}

.studentItem {
    display: flex;
    align-items: center;
    padding: 15px;
    background-color: #293241; /* Darker blue */
    margin-bottom: 10px;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

.studentItem:hover {
    background-color: #3D5A80; /* Slightly lighter on hover */
    transform: translateY(-3px);
}

.studentInfo {
    margin-left: 15px;
}

.studentName {
    color: #FFF;
    font-size: 18px;
    font-weight: 500; /* Medium weight */
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

/* Selected Student Content */
.selectedStudentContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

.subHeading {
    font-family: 'Montserrat', sans-serif; /* Use Montserrat for subheadings */
    font-size: 24px;
    font-weight: 700; /* Bold weight */
    color: #FFEEBC; /* Light cream color for better contrast */
    margin-bottom: 20px;
}

.violationsContainer {
    background-color: #293241; /* Dark background for contrast */
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    max-width: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 30px;
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

.violationsHeading {
    font-family: 'Montserrat', sans-serif; /* Use Montserrat for headings */
    font-size: 20px;
    font-weight: 700; /* Bold weight */
    color: #FFEEBC; /* Light cream color */
    margin-bottom: 15px;
}

.violationsText {
    font-size: 16px;
    color: #E0FBFC; /* Lighter blue for better readability */
    margin: 5px 0;
    font-weight: 400; /* Regular weight */
}

.totalPointsText {
    font-size: 18px;
    font-weight: 500; /* Medium weight */
    color: #FFD166; /* Bright yellow for emphasis */
    margin-top: 10px;
}

/* Chart Container */
.chartContainer {
    width: 100%;
    max-width: 500px;
    height: 300px;
    background-color: #293241; /* Matching the violations container */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

/* Bottom Navigation */
.bottomNav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #293241; /* Dark navigation bar */
    padding: 10px 0;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.3);
    font-family: 'Roboto', sans-serif; /* Consistent font */
}

.navItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    transition: color 0.3s ease;
}

.navItem:hover {
    color: #EE6C4D; /* Highlight on hover */
}

.navText {
    color: #FFF;
    font-size: 12px;
    margin-top: 5px;
    font-weight: 500; /* Medium weight */
}

/* Chart Text Styling */
.chartjs-render-monitor {
    font-family: 'Roboto', sans-serif; /* Ensure chart text uses Roboto */
}

/* Responsive Design */
@media (max-width: 768px) {
    .chartContainer {
        height: 250px;
    }

    .studentList {
        max-width: 90%;
    }

    .selectedStudentContent {
        max-width: 90%;
    }

    .mainHeading {
        font-size: 26px;
    }

    .subHeading {
        font-size: 22px;
    }
}

@media (max-width: 480px) {
    .mainHeading {
        font-size: 24px;
    }

    .subHeading {
        font-size: 20px;
    }

    .studentName {
        font-size: 16px;
    }

    .violationsHeading {
        font-size: 18px;
    }

    .violationsText {
        font-size: 14px;
    }

    .totalPointsText {
        font-size: 16px;
    }

    .navText {
        font-size: 10px;
    }
}
